body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  color: #444;
}

button {
  padding: 10px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  background-color: royalblue;
  color: white;
  border-radius: 5px;
  cursor: pointer;
}
button:hover {
  background-color: rgb(47, 81, 184);
  color: white;
}

h3,
h4 {
  margin-bottom: 0;
}

nav {
  border-bottom: 4px solid royalblue;
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #e9ebee;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
}

nav a {
  text-decoration: none;
  color: #444;
  font-weight: bold;
  padding: 16px 32px;
}

nav a:hover {
  background: royalblue;
  color: white;
  cursor: pointer;
}

#page-body {
  max-width: 700px;
  margin: auto;
  padding-top: 64px;
}

.article-list-item {
  text-decoration: none;
  color: #444;
  padding: 16px 0 0 0;
}
.article-list-item:hover {
  color: royalblue;
  text-decoration: underline;
}

.article-list-item p {
  padding-bottom: 32px;
  border-bottom: 2px solid #888;
}

#add-comment-form {
  border-bottom: 2px solid #888;
  padding: 16px;
}

#add-comment-form h3 {
  margin-bottom: 16px;
}

#add-comment-form input,
textarea {
  display: block;
  width: 300px;
  padding: 8px;
  border-radius: 8px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 2px solid #444;
  margin-bottom: 16px;
}

#add-comment-form button {
  width: 320px;
}

#upvotes-section button,
p {
  display: inline-block;
  margin-right: 16px;
}

.comment {
  border-bottom: 2px solid #444;
  padding: 16px;
}
